.boxPresentation{
  width: 100%;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  color: white;
  padding: 40px 0px 0px 50px;
}
.boxPresentation .textDate{
  font-weight: 300;
  font-size: 18px;
  margin: 6px 0px 50px;
  letter-spacing: 0.6px;
  font-family: 'Raleway', sans-serif;
}
.boxPresentation .textTitle{
  font-weight: 300;
  font-size: 32px;
  margin-bottom: 20px;
  letter-spacing: 0.6px;
  font-family: 'Raleway', sans-serif;
}
.boxPresentation .textMsg{
  font-weight: 300;
  font-size: 18px;
  margin-bottom: 10px;
  letter-spacing: 0.2px;
  font-family: 'Raleway', sans-serif;
}
.boxActionUser{
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.boxActionUser .sub{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.boxActionUser .sub img{
  width: 40px;
  border-radius: 40px;
  margin-right: 10px;
}
.boxActionUser .boxIcontActionUser{
  margin-left: 20px;
}
.boxPresentation .boxHoverOptions{
  width: 160px;
  background-color: white;
  position: absolute;
  right: 0px;
  color: rgba(0,0,0,0.6);
  padding: 24px;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);

  border-radius: 20px;

  transition: top 0.4s cubic-bezier(1,-0.47,.04,1.28);
}

.boxPresentation .boxHoverOptions div{
  cursor: pointer;
}

/* Tablet */
@media (max-width: 1180px) { 
  .boxPresentation{
    padding: 100px 50px 0px 50px;
  }
  .boxActionUser, .textDate{
    display: none;
  }
}
/* Mobile */
@media (max-width: 730px) { 
  .boxPresentation{
    padding: 30px 20px 0px 20px;
  }
}