.superBoxLogin{
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;

  font-family: 'Raleway', sans-serif;
}
.superBoxLogin .boxImage {
  width: 50%;
  height: 100%;
  display: flex;
  background-repeat: no-repeat;
  background-size: cover;
}
.superBoxLogin .boxLogin {
  width: 50%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}
.superBoxLogin .boxLogin .containerLogin{
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.superBoxLogin .boxLogin img{
  margin-bottom: 50px;
  width: 350px;
}
.superBoxLogin .boxLogin p{
  width: 350px;
  margin-bottom: 50px;
  font-size: 14px;
  font-weight: 600;
  color: rgba(0,0,0,0.6);
  letter-spacing: 0.2px;
  text-align: center;
}
.superBoxLogin .boxLogin .inputBoxLogin{
  width: 350px;
  margin-bottom: 20px;
  padding: 10px 10px;

  border: 0px solid rgba(0,0,0,0.6);
  border-bottom: 2px solid rgba(0,0,0,0.08);

  font-size: 16px;
  color: rgba(0,0,0,0.6);
  font-weight: 600;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 0.6px;

  text-decoration: none;
  outline: none;
}
.superBoxLogin .boxLogin .inputBoxLogin::placeholder{
  font-size: 16px;
  color: rgba(0,0,0,0.6);
  font-weight: 600;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 0.6px;

}
.superBoxLogin .boxLogin .boxCheckBox{
  width: 330px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  font-size: 13px;
  margin-top: 0px;
}
.superBoxLogin .boxLogin .boxCheckBox label{
  display: flex;
  align-items: center;
  cursor: pointer;
  font-family: 'Ubuntu', sans-serif;
  color: rgba(0,0,0,0.4);
}
.superBoxLogin .boxLogin .boxCheckBox input[type="checkbox"]{
  margin-right: 10px;
}
.superBoxLogin .boxLogin .boxCheckBox div{
  font-family: 'Ubuntu', sans-serif;
  color: rgba(0,0,0,0.4);
  font-size: 13px;
  cursor: pointer;
}
.inputSubmitLogin{
  min-width: 100px;
  min-height: 36px;

  padding: 8px 0px;
  margin-top: 50px;

  display: flex;
  align-items: center;
  justify-content: center;

  color: white;
  background-color: #F5821F;

  cursor: pointer;
  font-size: 13px;
  font-weight: 900;
  letter-spacing: 1px;

  border-radius: 3px;
}

@media (max-width: 768px) { 
  .superBoxLogin .boxImage {
    display: none;
  }
  .superBoxLogin .boxLogin {
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 428px) { 
  .superBoxLogin .boxLogin {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
}