.startTransparent{
  width: 100%;
  padding: 0px 0px 0px 50px;
}
.startTransparent .boxTitlePage{
  font-weight: 300;
  font-size: 32px;
  padding: 0px 0px 30px 0px;
  letter-spacing: 0.6px;
  font-family: 'Raleway', sans-serif;
  color: white;
}

.startTransparent .charts{
  width: 100%;


  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
}

.startTransparent .charts .subCharts{
  width: 100%;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: row-reverse;
}

.startTransparent .charts .subCharts .subChartsMini{
  width: 38%;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
}

.startTransparent .charts .subCharts .subChartsMini .chartHalfHeight{
  width: 100%;
  min-height: 180px;

  padding: 26px;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  background-color: white;

  border-radius: 20px;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);

  color: rgba(0,0,0,0.6);
}


.startTransparent .charts .subCharts .subChartsMini .changeColorChart{
  width: 46%;
  height: 150px;

  padding: 26px;
  margin: 20px 0px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background-color: #00cd9c;
  color: white;

  border-radius: 20px;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);
}
.startTransparent .charts .subCharts .subChartsMini .changeColorChart p{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.startTransparent .charts .subCharts .subChartsMini .changeColorChart div{
  line-height:28px;
}


.startTransparent .charts .subCharts .subChartFastButtons{
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
}

.startTransparent .charts .subCharts .subChartFastButtons .textTitle{
  width: 100%;
  padding: 10px 0px 20px;

  font-family: 'Raleway', sans-serif;
  font-size: 20px;
  color: white;
}
.startTransparent .charts .subCharts .subChartFastButtons .buttonFastButtons{
  width: 100%;
  min-height: 50px;

  padding: 0px 20px;
  margin-bottom: 16px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  border-radius: 20px;

  background-color: white;

  font-size: 15px;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);
  cursor: pointer;
}
.startTransparent .charts .subCharts .subChartFastButtons .buttonFastButtons p{
  margin-top: 3px;
  
}


.startTransparent .charts .subCharts .subChartsMini .smallChart{
  width: 46%;
  height: 150px;

  padding: 26px;
  margin-top: 20px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background-color:white;
  color: black;

  border-radius: 20px;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);
}
.startTransparent .charts .subCharts .subChartsMini .smallChart p{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.startTransparent .charts .subCharts .subChartsMini .smallChart div{
  line-height:28px;
}

.startTransparent .charts .subCharts .subChartsMini .chartFullHeight{
  width: 100%;
  min-height: 360px;

  padding: 26px;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  background-color: white;

  border-radius: 20px;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);

  color: rgba(0,0,0,0.6);
}


/*  */




/* Tablet */
@media (max-width: 1180px) { 
  .startTransparent{
    padding: 0px 50px 0px 50px;
  }
}
/* Tablet 2 */
@media (max-width: 900px) { 
  .startTransparent .charts .subCharts{
    flex-wrap: wrap;
    flex-direction: row-reverse;
  }
  .startTransparent .charts .subCharts .subChartsMini{
    width: 48%;

  }
  .startTransparent .charts .subCharts .subChartFastButtons{
    width: 100%;

  }
}
/* Mobile */
@media (max-width: 730px) { 
  .startTransparent{
    padding: 0px 20px 0px 20px;
  }
  .startTransparent .boxTitlePage{
    padding-top: 50px;
    padding-bottom: 0px;
  }
  .startTransparent .charts .subCharts .subChartsMini{
    width: 100%;
  }
}