@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;300&display=swap);
*{
  margin: 0;
  padding: 0;

  box-sizing: border-box;
}
html{
  height: 100%;
}
body{
  height: 100%;
  font-family: 'Ubuntu', sans-serif;
}
html, body{
  height: 100%;
}
.superBoxLogin{
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;

  font-family: 'Raleway', sans-serif;
}
.superBoxLogin .boxImage {
  width: 50%;
  height: 100%;
  display: flex;
  background-repeat: no-repeat;
  background-size: cover;
}
.superBoxLogin .boxLogin {
  width: 50%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}
.superBoxLogin .boxLogin .containerLogin{
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.superBoxLogin .boxLogin img{
  margin-bottom: 50px;
  width: 350px;
}
.superBoxLogin .boxLogin p{
  width: 350px;
  margin-bottom: 50px;
  font-size: 14px;
  font-weight: 600;
  color: rgba(0,0,0,0.6);
  letter-spacing: 0.2px;
  text-align: center;
}
.superBoxLogin .boxLogin .inputBoxLogin{
  width: 350px;
  margin-bottom: 20px;
  padding: 10px 10px;

  border: 0px solid rgba(0,0,0,0.6);
  border-bottom: 2px solid rgba(0,0,0,0.08);

  font-size: 16px;
  color: rgba(0,0,0,0.6);
  font-weight: 600;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 0.6px;

  text-decoration: none;
  outline: none;
}
.superBoxLogin .boxLogin .inputBoxLogin::-webkit-input-placeholder{
  font-size: 16px;
  color: rgba(0,0,0,0.6);
  font-weight: 600;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 0.6px;

}
.superBoxLogin .boxLogin .inputBoxLogin:-ms-input-placeholder{
  font-size: 16px;
  color: rgba(0,0,0,0.6);
  font-weight: 600;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 0.6px;

}
.superBoxLogin .boxLogin .inputBoxLogin::-ms-input-placeholder{
  font-size: 16px;
  color: rgba(0,0,0,0.6);
  font-weight: 600;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 0.6px;

}
.superBoxLogin .boxLogin .inputBoxLogin::placeholder{
  font-size: 16px;
  color: rgba(0,0,0,0.6);
  font-weight: 600;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 0.6px;

}
.superBoxLogin .boxLogin .boxCheckBox{
  width: 330px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  font-size: 13px;
  margin-top: 0px;
}
.superBoxLogin .boxLogin .boxCheckBox label{
  display: flex;
  align-items: center;
  cursor: pointer;
  font-family: 'Ubuntu', sans-serif;
  color: rgba(0,0,0,0.4);
}
.superBoxLogin .boxLogin .boxCheckBox input[type="checkbox"]{
  margin-right: 10px;
}
.superBoxLogin .boxLogin .boxCheckBox div{
  font-family: 'Ubuntu', sans-serif;
  color: rgba(0,0,0,0.4);
  font-size: 13px;
  cursor: pointer;
}
.inputSubmitLogin{
  min-width: 100px;
  min-height: 36px;

  padding: 8px 0px;
  margin-top: 50px;

  display: flex;
  align-items: center;
  justify-content: center;

  color: white;
  background-color: #F5821F;

  cursor: pointer;
  font-size: 13px;
  font-weight: 900;
  letter-spacing: 1px;

  border-radius: 3px;
}

@media (max-width: 768px) { 
  .superBoxLogin .boxImage {
    display: none;
  }
  .superBoxLogin .boxLogin {
    width: 100%;
    height: 100%;
  }
}
@media (max-width: 428px) { 
  .superBoxLogin .boxLogin {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
}
.navMenuBar{
  width: 300px;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;

  position: fixed;
  margin-right: 880px;

}
.navMenuBar .boxLogoAdmin{
  width: 100%;
  padding: 20px 0px 30px;

  display: flex;
  align-items: center;
  justify-content: center;
}
.navMenuBar img{
  width: 300px;
  height: 70px;
  cursor: pointer;
}

.navMenuBar .hoverLink{
  width: 100%;
  height: 50px;

  margin: 0px 0px 10px 0px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;
}
.navMenuBar .hoverLink section{
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.navMenuBar .hoverLink .roundIcon{
  width: 50px;
  height: 50px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(0,0,0,0.4);
}
.navMenuBar .hoverLink .yess{
  background-color: #F5845C;
  color: white;
}
.navMenuBar .hoverLink .text{
  padding-left: 14px;
  color: rgba(0,0,0,0.6);
  font-size: 17px;
  font-weight: 300;
}
.navMenuBar .hoverLink .arrowIcon{
  color: rgba(0,0,0,0.4);
}


/* Desktop */
@media (max-width: 1180px) { 
  .navMenuBar{
    display: none;
  }
}
.backOrange{
  width: 100%;
  height: 100%;

  background: rgb(245,132,92);
  background: linear-gradient(38deg, rgba(245,132,92,1) 0%, rgba(215,108,97,1) 100%);

  border-radius: 0px 0px 0px 40px;

  position: fixed;
  bottom: 130px;
  z-index: -9999;
}

/* Tablet */
@media (max-width: 1180px) { 
  .backOrange{
    border-radius: 0px 0px 40px 40px;
  }
}
/* Mobile */
@media (max-width: 500px) { 
  .boxInitial .responsiveMeta{
    width: 100%;
  }
}

.boxFixedMenuMedia{
  width: 100%;
  height: 60px;

  display: none;

  position: fixed;
  z-index: 999;
  padding: 0px 50px;

  background-color: white;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.25);

}

.boxFixedMenuMedia .boxShowed{
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.boxFixedMenuMedia .boxShowed div{
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  cursor: pointer;
}

.boxFixedMenuMedia .boxShowed img{
  max-height: 50px;
  cursor: pointer;
}


/* Tablet */
@media (max-width: 1180px) { 
  
  .boxFixedMenuMedia{
    display: flex;
  }
}
/* Mobile */
@media (max-width: 730px) { 
  .boxFixedMenuMedia{
    padding: 0px 20px;
  }
  
}
.boxPresentation{
  width: 100%;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  color: white;
  padding: 40px 0px 0px 50px;
}
.boxPresentation .textDate{
  font-weight: 300;
  font-size: 18px;
  margin: 6px 0px 50px;
  letter-spacing: 0.6px;
  font-family: 'Raleway', sans-serif;
}
.boxPresentation .textTitle{
  font-weight: 300;
  font-size: 32px;
  margin-bottom: 20px;
  letter-spacing: 0.6px;
  font-family: 'Raleway', sans-serif;
}
.boxPresentation .textMsg{
  font-weight: 300;
  font-size: 18px;
  margin-bottom: 10px;
  letter-spacing: 0.2px;
  font-family: 'Raleway', sans-serif;
}
.boxActionUser{
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.boxActionUser .sub{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.boxActionUser .sub img{
  width: 40px;
  border-radius: 40px;
  margin-right: 10px;
}
.boxActionUser .boxIcontActionUser{
  margin-left: 20px;
}
.boxPresentation .boxHoverOptions{
  width: 160px;
  background-color: white;
  position: absolute;
  right: 0px;
  color: rgba(0,0,0,0.6);
  padding: 24px;
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);

  border-radius: 20px;

  transition: top 0.4s cubic-bezier(1,-0.47,.04,1.28);
}

.boxPresentation .boxHoverOptions div{
  cursor: pointer;
}

/* Tablet */
@media (max-width: 1180px) { 
  .boxPresentation{
    padding: 100px 50px 0px 50px;
  }
  .boxActionUser, .textDate{
    display: none;
  }
}
/* Mobile */
@media (max-width: 730px) { 
  .boxPresentation{
    padding: 30px 20px 0px 20px;
  }
}
.startTransparent{
  width: 100%;
  padding: 0px 0px 0px 50px;
}
.startTransparent .boxTitlePage{
  font-weight: 300;
  font-size: 32px;
  padding: 0px 0px 30px 0px;
  letter-spacing: 0.6px;
  font-family: 'Raleway', sans-serif;
  color: white;
}

.startTransparent .charts{
  width: 100%;


  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
}

.startTransparent .charts .subCharts{
  width: 100%;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: row-reverse;
}

.startTransparent .charts .subCharts .subChartsMini{
  width: 38%;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;
}

.startTransparent .charts .subCharts .subChartsMini .chartHalfHeight{
  width: 100%;
  min-height: 180px;

  padding: 26px;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  background-color: white;

  border-radius: 20px;
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);

  color: rgba(0,0,0,0.6);
}


.startTransparent .charts .subCharts .subChartsMini .changeColorChart{
  width: 46%;
  height: 150px;

  padding: 26px;
  margin: 20px 0px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background-color: #00cd9c;
  color: white;

  border-radius: 20px;
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);
}
.startTransparent .charts .subCharts .subChartsMini .changeColorChart p{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.startTransparent .charts .subCharts .subChartsMini .changeColorChart div{
  line-height:28px;
}


.startTransparent .charts .subCharts .subChartFastButtons{
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
}

.startTransparent .charts .subCharts .subChartFastButtons .textTitle{
  width: 100%;
  padding: 10px 0px 20px;

  font-family: 'Raleway', sans-serif;
  font-size: 20px;
  color: white;
}
.startTransparent .charts .subCharts .subChartFastButtons .buttonFastButtons{
  width: 100%;
  min-height: 50px;

  padding: 0px 20px;
  margin-bottom: 16px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  border-radius: 20px;

  background-color: white;

  font-size: 15px;
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);
  cursor: pointer;
}
.startTransparent .charts .subCharts .subChartFastButtons .buttonFastButtons p{
  margin-top: 3px;
  
}


.startTransparent .charts .subCharts .subChartsMini .smallChart{
  width: 46%;
  height: 150px;

  padding: 26px;
  margin-top: 20px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  background-color:white;
  color: black;

  border-radius: 20px;
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);
}
.startTransparent .charts .subCharts .subChartsMini .smallChart p{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.startTransparent .charts .subCharts .subChartsMini .smallChart div{
  line-height:28px;
}

.startTransparent .charts .subCharts .subChartsMini .chartFullHeight{
  width: 100%;
  min-height: 360px;

  padding: 26px;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  background-color: white;

  border-radius: 20px;
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);

  color: rgba(0,0,0,0.6);
}


