/* Tablet */
@media (max-width: 1180px) { 
  .startTransparent{
    padding: 0px 50px 0px 50px;
  }
}
/* Tablet 2 */
@media (max-width: 900px) { 
  .startTransparent .charts .subCharts{
    flex-wrap: wrap;
    flex-direction: row-reverse;
  }
  .startTransparent .charts .subCharts .subChartsMini{
    width: 48%;

  }
  .startTransparent .charts .subCharts .subChartFastButtons{
    width: 100%;

  }
}
/* Mobile */
@media (max-width: 730px) { 
  .startTransparent{
    padding: 0px 20px 0px 20px;
  }
  .startTransparent .boxTitlePage{
    padding-top: 50px;
    padding-bottom: 0px;
  }
  .startTransparent .charts .subCharts .subChartsMini{
    width: 100%;
  }
}
