.boxDiary{
  width: 100%;
  height: 100%;

  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.boxDiary .responsiveMeta{
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  position: relative;
}
.boxDiary .responsiveMeta article{

  display: flex;
  align-items:flex-start;
  justify-content: flex-end;
  flex-direction: column;

  position: relative;
}

/* Dekstop */
@media (max-width: 1400px) { 
  .boxDiary .responsiveMeta{
    width: 1280px;
  }
  .boxDiary .responsiveMeta article{
    width: 830px;
  }
}
/* Desktop */
@media (max-width: 1280px) { 
  .boxDiary .responsiveMeta{
    width: 1180px;
  }
  .boxDiary .responsiveMeta article{
    width: 830px;
  }
}
/* Tablet */
@media (max-width: 1180px) { 
  .boxDiary .responsiveMeta{
    width: 100%;
  }
  .boxDiary .responsiveMeta article{
    width: 100%;
  }
}
/* Mobile */
@media (max-width: 730px) { 
  .boxDiary .responsiveMeta{
    width: 100%;
  }
}