.navMenuBar{
  width: 300px;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;

  position: fixed;
  margin-right: 880px;

}
.navMenuBar .boxLogoAdmin{
  width: 100%;
  padding: 20px 0px 30px;

  display: flex;
  align-items: center;
  justify-content: center;
}
.navMenuBar img{
  width: 300px;
  height: 70px;
  cursor: pointer;
}

.navMenuBar .hoverLink{
  width: 100%;
  height: 50px;

  margin: 0px 0px 10px 0px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  cursor: pointer;
}
.navMenuBar .hoverLink section{
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.navMenuBar .hoverLink .roundIcon{
  width: 50px;
  height: 50px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(0,0,0,0.4);
}
.navMenuBar .hoverLink .yess{
  background-color: #F5845C;
  color: white;
}
.navMenuBar .hoverLink .text{
  padding-left: 14px;
  color: rgba(0,0,0,0.6);
  font-size: 17px;
  font-weight: 300;
}
.navMenuBar .hoverLink .arrowIcon{
  color: rgba(0,0,0,0.4);
}


/* Desktop */
@media (max-width: 1180px) { 
  .navMenuBar{
    display: none;
  }
}