.superFatherDiaryBox{
  width: 100%;
}

.superFatherDiaryBox .boxTitleComp{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 0px 0px 50px 50px;

  color: white;
  font-size: 32px;
  font-weight: 300;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 0.6px;
}

.superFatherDiaryBox .boxTitleComp .buttonAction{
  display: flex;
  align-items: center;

  padding: 10px 16px;

  color: rgba(0,0,0,0.45);
  font-size: 14px;
  font-weight: 900;
  font-family: 'Raleway', sans-serif;
  letter-spacing: 0.6px;
  
  background-color: white;

  border-radius: 10px;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);

  cursor: pointer;
}

.superFatherDiaryBox .containerCaleandar{
  min-height: 400px;
  margin-left: 50px;
  padding: 30px;

  background-color: white;

  border-radius: 20px;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);
}

.superFatherDiaryBox .containerCaleandarInList{
  margin-left: 50px;
  padding: 30px;

  background-color: white;

  border-radius: 20px;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);
}
 .floatStyleBox{
  width: calc(100% - 20px);
  height: calc(100% - 20px);

  overflow: scroll;

  position: absolute;
  top: 0;
  left: 0;

  margin: 10px;
}
.superFatherDiaryBox .containerCaleandarInList .titleBoxList{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  padding: 0px 0px 22px;
}
.superFatherDiaryBox .containerCaleandarInList .titleBoxList section{
  padding-top: 4px;
  font-size: 28px;
  font-weight: 800;
}
.superFatherDiaryBox .containerCaleandarInList .titleBoxList section span{
  font-size: 14px;
  font-weight: 500;
  color: yellowgreen;
}
.superFatherDiaryBox .containerCaleandarInList .titleBoxList .boxButtonsActions{

  display: flex;
  min-height: 42px;

}
.todayButton{
  min-width: 64px;
  min-height: 30px;

  margin-right: 12px;

  display: flex;
  align-items: center;
  justify-content: center;

  color: white;
  background-color: #76818c;
  border-radius: 5px;
}

.goBack{
  display: flex;
  align-items: center;
  justify-content: center;

  min-width: 46px;
  min-height: 40px;
  color: white;

  background-color: #2b3e4f;
  border-radius: 5px 0px 0px 5px;
}
.goHead{
  display: flex;
  align-items: center;
  justify-content: center;

  min-width: 46px;
  min-height: 40px;
  color: white;

  background-color: #2b3e4f;
  border-radius: 0px 5px 5px 0px;
}
.hoverActions:hover{
  background-color: #1d2b36;
  cursor: pointer;
}

.containerCaleandarInList table {
  width: 100%;
  border: 2px solid #ebf0f4;
  border-collapse: collapse;
}

.containerCaleandarInList table tbody tr {
  cursor: pointer;
}
.containerCaleandarInList table .th{
  font-weight: 500;
  color: #1d2b36;
  font-size: 14px;
  background-color: white;
}
.containerCaleandarInList table, tr, th, td{
  min-height: 40px;
  border: 1.4px solid #ebf0f4;
  border-collapse: collapse;
  font-weight: 300;
  font-size: 14px;
  text-align: center;
  padding: 16px 0px;
}
.containerCaleandarInList tr:nth-child(odd) {
  background-color: #f5f6fa;
}



.fatherBoxDiaryInfo{
  width: 100%;
  height: 100%;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: rgba(0,0,0,0.2);

  position: fixed;
  top: 0;
  left: 0;
}
.fatherBoxDiaryInfo .boxDiaryInfo{
  width: 560px;
  min-height: 400px;
  max-height: calc(100% - 20px);

  padding: 30px;
  margin: 10px;

  background-color: white;

  border-radius: 20px;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.25);
  overflow: scroll;
}
.fatherBoxDiaryInfo .boxDiaryInfo .boxTitle{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.fatherBoxDiaryInfo .boxDiaryInfo .boxTitle .textTitle{
  font-size: 22px;
  font-family: 'Ubuntu', sans-serif;
  letter-spacing: 1px;
  font-weight: 500;
}
.fatherBoxDiaryInfo .boxDiaryInfo .boxTitle .textTitle span{
  font-size: 14px;
  font-weight: 400;
  letter-spacing: normal;
}
.boxForm{
  width: 100%;
  padding-top: 15px;
}
.boxForm p{
  padding: 15px 0px 8px;
}
.boxForm .newFormInput1 {
  width: 100%;
  min-height: 38px;
  display: flex;
  border: 1px solid rgba(0,0,0,0.2);
  padding: 10px 12px;
  margin-bottom: 8px;

  border-radius: 8px;
  
  font-size: 14px;
  font-weight: 400;
  color: rgba(0,0,0,0.8);
}
.boxForm .newFormInput1 input{
  width: 100%;
  border: 0px;
  padding: 0px 10px;
  font-size: 14px;
  font-weight: 300;
  letter-spacing: 0.4px;
  color: rgba(0,0,0,0.8);

  outline: none;
  text-decoration: none;
}
.boxForm .newFormInput1 textarea{
  width: 100%;
  min-height: 200px;
  max-height: 200px;
  border: 0px;
  padding: 6px 0px;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.4px;
  color: rgba(0,0,0,0.6);
  font-family: 'Ubuntu', sans-serif;

  outline: none;
  text-decoration: none;
  overflow: scroll;
}
.boxForm .subBoxForm2{
  width: 100%;

  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.boxForm .subBoxForm2 .newFormInput2{
  width: 49%;
  min-height: 38px;
  padding: 2px 12px;
  margin-bottom: 8px;

  display: flex;
  align-items: center;
  justify-content: flex-start;

  border: 1px solid rgba(0,0,0,0.2);

  font-size: 14px;
  font-weight: 400;
  color: rgba(0,0,0,0.8);

  border-radius: 8px;
}
.boxForm .subBoxForm2 .newFormInput2 input, select{
  width: 100%;
  outline: none;
  text-decoration: none;
  border: 0px;
  margin-left: 4px;
}
.boxActionButtons{
  width: 100%;
  
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-wrap: wrap;

}
.hoverMoreInfo{
  width: 100%;

  padding: 8px;
  margin-bottom: 10px;

  display: flex;
  align-items: center;
  justify-content: center;

  border-radius: 8px;

  color: rgba(0,0,0,0.45);

}
.hoverMoreInfo:hover{
  transition: all 0.2s ease-in-out;
  background-color: rgba(249, 131, 99, 0.2);
  cursor: pointer;
  color: #1d2b36;
}
.buttonDone{
  min-width: 140px;
  min-height: 38px;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: #f98363;

  border-radius: 8px;
  color: white;
  font-size: 14px;
  letter-spacing: 0.6px;

  cursor: pointer;
}


/* Tablet */
@media (max-width: 1180px) { 
  .superFatherDiaryBox{
    padding-right: 50px;
  }
}
/* Mobile */
@media (max-width: 730px) { 
  .superFatherDiaryBox{
    padding: 0px 20px 0px 20px;
  }
  
  .superFatherDiaryBox .boxTitleComp{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;

    padding: 50px 0px 40px 0px;
  }
  
  .superFatherDiaryBox .boxTitleComp .buttonAction{

    display: none;

  }
  
  .superFatherDiaryBox .containerCaleandar{
    margin-left: 0px;
    display: none;
  }
  .superFatherDiaryBox .containerCaleandarInList{
    margin-left: 0px;
    display: none;
  }
  
}