
.boxFixedMenuMedia{
  width: 100%;
  height: 60px;

  display: none;

  position: fixed;
  z-index: 999;
  padding: 0px 50px;

  background-color: white;

  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.45);
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.25);

}

.boxFixedMenuMedia .boxShowed{
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.boxFixedMenuMedia .boxShowed div{
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  cursor: pointer;
}

.boxFixedMenuMedia .boxShowed img{
  max-height: 50px;
  cursor: pointer;
}


/* Tablet */
@media (max-width: 1180px) { 
  
  .boxFixedMenuMedia{
    display: flex;
  }
}
/* Mobile */
@media (max-width: 730px) { 
  .boxFixedMenuMedia{
    padding: 0px 20px;
  }
  
}